import React, {
  useCallback, useEffect, useMemo, useState
} from 'react';
import { useTranslation } from 'react-i18next';
import {
  Box, Button, FormControl, FormLabel, Input, Select, useStyleConfig
} from '@chakra-ui/react';
import PhoneInput from 'react-phone-number-input';
import 'react-phone-number-input/style.css';
import { shallowEqual } from 'react-redux';
import { format, subYears } from 'date-fns';
import { useNavigate } from 'react-router-dom';
import SsnInput from '../../../../components/SsnInput';
import MiniBox from '../../../../layout/MiniBox';
import DatePicker from '../../../../components/DatePicker';
import { useAppAsyncDispatch, useAppSelector } from '../../../../hooks/store';
import { updateSelectedBusinessOwner } from '../../../../store/actionCreators/business';
import { documentTypes } from '../../../../utils/general';

type KybPersonData = {
  firstName: string,
  lastName: string
  phone: string,
  email: string,
  dateOfBirth: string,
  idNumber: string
  idType: string
}

/**
 * Kyb Owner Details - Component for collecting the details of the bussiness owners.
 * @module KYB
 * @returns {JSX.Element}
 */
const OwnerDetails = () => {
  const { t } = useTranslation();
  const navigate = useNavigate();
  const dispatch = useAppAsyncDispatch();
  const [formData, updateFormData] = useState<KybPersonData>({
    firstName: '',
    lastName: '',
    phone: '',
    email: '',
    dateOfBirth: '',
    idNumber: '',
    idType: 'ssn'
  });
  const [isSSNValid, updateSSNValidation] = useState(false);

  const inputStyle:any = useStyleConfig('Input', {
    variant: 'primary'
  });

  const selectedBusinessOwner = useAppSelector((store) => store.business.selectedBusinessOwner, shallowEqual);

  const isValid = useMemo(() => {
    /* eslint-disable no-useless-escape */
    const emailRegex = /^\w+([\.-]?\w+)*@\w+([\.-]?\w+)*(\.\w\w+)+$/;

    return formData.firstName.length !== 0 && formData.lastName.length !== 0 && isSSNValid && formData?.idType.length !== 0 && formData?.email.length !== 0 && emailRegex.test(formData.email);
  }, [formData, isSSNValid]);

  useEffect(() => {
    if (selectedBusinessOwner) {
      updateFormData({
        firstName: selectedBusinessOwner.person.firstName,
        lastName: selectedBusinessOwner.person.lastName,
        email: selectedBusinessOwner.person.email,
        phone: selectedBusinessOwner.person.phone,
        dateOfBirth: selectedBusinessOwner.person.dateOfBirth,
        idNumber: selectedBusinessOwner.person.idNumber,
        idType: selectedBusinessOwner.person.idType
      });

      if (selectedBusinessOwner.person.idNumber.length !== 0) { updateSSNValidation(true); }
    }
  }, [selectedBusinessOwner]);

  /**
   * letter validation with state update
   */
  const allowLetterValidation = useCallback(
    (nameType: keyof KybPersonData, updatedName: string) => {
      if (/^[a-zA-Z\s]+$/.test(updatedName) || updatedName === '') {
        updateFormData({
          ...formData,
          [nameType]: updatedName
        });
      }
    },
    [formData],
  );

  const submit = useCallback(() => {
    let owner: any = {};
    if (selectedBusinessOwner) {
      owner = {
        id: selectedBusinessOwner.id,
        ownership: selectedBusinessOwner.ownership,
        person: {
          ...formData,
          address: selectedBusinessOwner.person.address
        }
      };
    } else {
      owner = {
        person: {
          ...formData,
          address: {
            line1: '',
            line2: '',
            city: '',
            state: '',
            country: '',
            postalCode: '',
            addressType: 'legal'
          }
        }
      };
    }
    dispatch(updateSelectedBusinessOwner(owner));
    setTimeout(() => {
      navigate('/ao/kyb/ownerAddress');
    }, 0);
  }, [selectedBusinessOwner, formData]);

  return (
    <div className="page">
      <form onSubmit={(e) => {
        e.preventDefault();
        submit();
      }}
      >
        <MiniBox
          varient="box"
          heading={t('owner_personal_details.title')}
          footer={(
            <Button data-testid="submit-btn" width="100%" variant="primary" type="submit" disabled={!isValid}>
              {t('owner_personal_details.ctaText')}
            </Button>
)}
        >
          <Box width="100%">

            <FormControl variant="primary">
              <FormLabel variant="primary">
                {t('owner_personal_details.firstNameLabel')}
              </FormLabel>
              <Input
                data-testid="firstName"
                value={formData.firstName}
                onChange={(e) => {
                  allowLetterValidation('firstName', e.target.value);
                }}
                variant="primary"
              />
            </FormControl>

            <FormControl variant="primary">
              <FormLabel variant="primary">
                {t('owner_personal_details.lastNameLabel')}
              </FormLabel>
              <Input
                data-testid="lastName"
                value={formData.lastName}
                onChange={(e) => {
                  allowLetterValidation('lastName', e.target.value);
                }}
                variant="primary"
              />
            </FormControl>

            <FormControl variant="primary">
              <FormLabel variant="primary">
                {t('owner_personal_details.mobileLabel')}
              </FormLabel>
              <PhoneInput
                data-testid="mobileInput"
                style={inputStyle.field}
                className="country-input"
                international
                limitMaxLength
                defaultCountry="US"
                countryCallingCodeEditable={false}
                value={formData.phone}
                countries={['US', 'IN']}
                onChange={(v) => {
                  updateFormData({
                    ...formData,
                    phone: v || ''
                  });
                }}
              />
            </FormControl>

            <FormControl variant="primary">
              <FormLabel variant="primary">
                {t('owner_personal_details.emailLabel')}
              </FormLabel>
              <Input
                data-testid="email"
                variant="primary"
                type="email"
                onChange={(e) => {
                  updateFormData({
                    ...formData,
                    email: e.target.value
                  });
                }}
                value={formData?.email}
              />
            </FormControl>

            <FormControl variant="primary">
              <FormLabel variant="primary">
                {t('owner_personal_details.dobLabel')}
              </FormLabel>
              <DatePicker
                maxDate={subYears(new Date(), 18)}
                dateFormat="MM / dd / yyyy"
                peekNextMonth
                showMonthDropdown
                showYearDropdown
                dropdownMode="select"
                value={formData.dateOfBirth}
                onChange={(date) => {
                  if (date) {
                    updateFormData({
                      ...formData,
                      dateOfBirth: format(date, 'yyyy-MM-dd')
                    });
                  }
                }}
              />
            </FormControl>

            <FormControl variant="primary">
              <FormLabel variant="primary">
                {t('owner_personal_details.documentType')}
              </FormLabel>
              <Select
                data-testid="document-type"
                variant="primary"
                value={formData?.idType}
                onChange={(e:any) => updateFormData({
                  ...formData,
                  idType: e.target.value
                })}
                placeholder={t('owner_personal_details.documentType')}
              >
                {
                  documentTypes.map((d) => (
                    <option key={d.type} value={d.type}>
                      {d.label}
                    </option>
                  ))
                }
              </Select>
            </FormControl>

            <FormControl variant="primary">
              <FormLabel variant="primary">
                {t(`owner_personal_details.${formData?.idType}`, 'Social Security Number')}
              </FormLabel>
              <SsnInput
                isLast4={false}
                idType={formData?.idType}
                value={formData.idNumber}
                onChange={(_, v, validation) => {
                  updateFormData({
                    ...formData,
                    idNumber: v
                  });
                  updateSSNValidation(validation);
                }}
              />
            </FormControl>

          </Box>
        </MiniBox>
      </form>
    </div>
  );
};

export default OwnerDetails;
