import { Input } from '@chakra-ui/react';
import { format } from 'date-fns';
import React, { useEffect, useState } from 'react';
import RDatePicker, { ReactDatePickerProps } from 'react-datepicker';
import 'react-datepicker/dist/react-datepicker.css';
import './DatePicker.css';

const CustomInput = React.forwardRef((props: any, ref: any) => <Input {...props} ref={ref} data-testid="date-input" variant="primary" />);
/**
 * Datepicker with custom input element
 * @param {ReactDatePickerProps} props
 * @returns {React.ReactNode}
 */
const DatePicker:React.FC<ReactDatePickerProps> = ({ value, onChange, ...props }) => {
  const [selectedDate, updateDate] = useState<string | undefined>(value);

  useEffect(() => {
    if (value) {
      updateDate(format(new Date(value), 'MM / dd / yyyy'));
    }
  }, [value]);

  return (
    <RDatePicker
      {...props}
      customInput={<CustomInput />}
      dateFormat="MM / dd / yyyy"
      value={selectedDate}
      onChange={(date, e) => {
        if (date) { updateDate(format(date, 'MM / dd / yyyy')); }
        onChange(date, e);
      }}
    />
  );
};

export default DatePicker;
